import React, { ReactNode } from 'react';
import { Text } from 'core/src/typography';

import styles from './Item.module.scss';

interface ItemProps {
  /** Контент элемента выпадающего списка */
  children: ReactNode;

  /** Флаг кликабельности элемента */
  disabled?: boolean;

  /** Иконка элемента выпадающего списка */
  icon?: ReactNode;

  /** Элемент с названием горячей клавиши */
  hotkey?: ReactNode;

  /** Колбэк нажатия на элемент выпадающего списка */
  onClick(): void;
}

/** Элемент выпадающего списка */
const Item: React.FC<ItemProps> = ({
  children,
  disabled,
  onClick,
  icon,
  hotkey
}) => (
  <button
    className={ styles.item }
    onClick={ onClick }
    type="button"
    disabled={ disabled }
  >
    <div className={ styles.icon }>{ icon }</div>
    <div className={ styles.text }>
      <Text size="small" color="primary">{ children }</Text>
    </div>
    <Text size="small" color="tertiary">{ hotkey }</Text>
  </button>
);

export default Item;
